import { net } from '@/service/net.js'
import config from '@/common/config'
import conn_util from '@/common/es/conn_util'

import axios from 'axios'

let url = config.baseURL + config.apiURL + '/member/'
const _url = url 

export default {
  url,
  getList_Member(condition, status) {
    url = _url + conn_util.buildURL(condition, status)

    /*
    config['transformResponse'] = [
      function(data) {
        return data.data
      }
    ]
    console.log({
      url,
      method: 'GET',
      auth: conn_util.getAuth()
    })
    const a = axios({
      url,
      method: 'GET',
      auth: conn_util.getAuth(),
    })
    a.response.use(res => {
      return res.data
    })
    
    return a
    */
    return net({
      url,
      method: 'GET',
      auth: conn_util.getAuth()
    })
  },
  trash(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'PUT',
      data: {
        status: 'False'
      },
      auth: conn_util.getAuth()
    })
  },
  add(data) {
    url = _url
    
    return net({
      url,
      method: 'POST',
      data,
      auth: conn_util.getAuth()
    })
  },
  edit(id, data) {

    url = _url + id + '/'
    data.status = 'True'

    return net({
      url,
      method: 'PUT',
      data
    })
  },
  getOne(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'GET',
      auth: conn_util.getAuth()
    })
  }
}


